@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
/* colors */
.toolbar {
  position: fixed;
  left: 0;
  right: 0;
  background-color: #ffffff;
  z-index: 33;
  display: flex;
  padding: 12px;
  margin-bottom: 8px;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15), 0 2px 3px rgba(0, 0, 0, 0.2); }
  .toolbar__buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto; }
  .toolbar__toggle {
    border: 1px solid #303030;
    border-radius: 4px;
    padding: 4px;
    cursor: pointer;
    display: none; }

@media (max-width: 768px) {
  .toolbar__buttons {
    position: absolute;
    right: 20px;
    top: 35px;
    height: 90px;
    width: 80px;
    background: #333;
    border-radius: 4px;
    flex-direction: column;
    justify-content: center; }
    .toolbar__buttons-hide {
      display: none; }
  .toolbar__toggle {
    margin-left: auto;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center; }
  .toolbar .button {
    color: white;
    background: transparent; } }

